require('./jquery.validate');
require('./addRule');


module.exports = {
	// 
	modifyRules: function(validateObj,opts){

       validateObj.settings = $.extend(true,{},validateObj.settings,opts)
    },
    // 
    password: {
    	rules: {
    		password: true,
			unPureLetter: true,
			unPureNum: true,
			rangelength:[6,20]
    	},
    	messages: {
    		required: ''
    	}
    },
    r_password: {
    	messages: {
    		required: '',
			equalTo: ''
    	}
    },
    verify_code: {
    	messages: {
    		required: ''
    	}
    },
    username: {
    	rules: {
			username: true
    	},
    	messages: {
    		required: '',
    		minlength: '6-20',
    		maxlength: '6-20'
    	}
    }
}


