jQuery.validator.addMethod("scale", function(value, element) {
	var reg = true,
		arr = value.split(',');

	for(var i = 0, len = arr.length; i < len; i++) {
		var num = arr[i] - 0;

		if(num !== num || num < 0) {
			reg = false;
			break;
		}
	}
    return this.optional(element) || reg;
}, "");

