$.validator.setDefaults({
    errorElement: 'span',
    ignore: '.ignore',
    errorClass: 'error validator-error',
    errorPlacement: function(error, element) {

        if (element.attr('type') == 'radio' ||
            element.attr('type') == 'checkbox' ||
            element.hasClass('hidden-txt') ||
            element[0].tagName == 'SELECT' ||
            element.hasClass('validate-parent')
        ) {

            error.appendTo(element.parent().parent());
        } else {
            error.appendTo(element.parent());
        }
    },
    messages: {
        password: {
            rangelength: '6-20',
            maxlength: '6-20',
            minlength: '6-20',
            unPureLetter: '',
            unPureNum: '',
            password: '6-20'
        }
    }
})



